import React from 'react'
import { Link } from 'react-router-dom'

export default function Breadcrumb({ props }) {
    return (
        <div className='px-7 rounded-sm md:mt-4'>
            <div className='text-md dark:text-mainText'>
                {props ?
                    props?.map((prop, i) =>
                        <span key={i}>
                            {prop?.header ?
                                <>
                                    <Link to={`/${prop.link}`} className={`${i === props.length - 1 ? "opacity-60" : "hover:underline text-blue-900 dark:text-blue-500"}`}> {prop.header}</Link> {i === props?.length - 1 ? "" : "/"}
                                </>
                                :
                                <></>
                            }
                        </span>
                    )
                    :
                    <></>
                }
            </div>
        </div>
    )
}
